"use client";

import React from "react";
import { Card } from "@/components/ui/Card";
import { Users, ShoppingBag, DollarSign, TrendingUp } from "lucide-react";
import { formatCurrency } from "@/lib/utils";

export default function AdminDashboard() {
    const stats = [
        { title: "Total Revenue", value: formatCurrency(124500), change: "+12.5%", icon: <DollarSign className="w-6 h-6 text-success" /> },
        { title: "Active Orders", value: "24", change: "+4", icon: <ShoppingBag className="w-6 h-6 text-primary" /> },
        { title: "Total Users", value: "1,203", change: "+18%", icon: <Users className="w-6 h-6 text-secondary" /> },
        { title: "Conversion Rate", value: "3.2%", change: "+0.4%", icon: <TrendingUp className="w-6 h-6 text-accent" /> },
    ];

    return (
        <div className="space-y-8">
            <div>
                <h1 className="text-3xl font-display font-bold">Dashboard</h1>
                <p className="text-text-secondary">Overview of your store's performance.</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                {stats.map((stat, index) => (
                    <Card key={index} className="p-6">
                        <div className="flex items-center justify-between mb-4">
                            <div className="p-3 bg-surface-elevated rounded-lg">
                                {stat.icon}
                            </div>
                            <span className="text-sm font-medium text-success bg-success/10 px-2.5 py-0.5 rounded-full">
                                {stat.change}
                            </span>
                        </div>
                        <h3 className="text-2xl font-bold mb-1">{stat.value}</h3>
                        <p className="text-sm text-text-secondary">{stat.title}</p>
                    </Card>
                ))}
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                <Card className="p-6 min-h-[300px]">
                    <h3 className="text-lg font-bold mb-4">Recent Orders</h3>
                    <div className="flex items-center justify-center h-48 text-text-tertiary">
                        Chart placeholder
                    </div>
                </Card>
                <Card className="p-6 min-h-[300px]">
                    <h3 className="text-lg font-bold mb-4">Traffic Overview</h3>
                    <div className="flex items-center justify-center h-48 text-text-tertiary">
                        Chart placeholder
                    </div>
                </Card>
            </div>
        </div>
    );
}
